export const margin = 10

const getOrientation = (width, height) => ({
  height,
  width,
  availableHeight: (height - (margin * 2)),
  availableWidth: (width - (margin * 2))
})

const getFormat = (width, height) => ({
  portrait: getOrientation(width, height),
  landscape: getOrientation(height, width),
})

export const formats = {
  a4: getFormat(210, 297),
  a3: getFormat(297, 420),
}

export const orientations = [
  'portrait',
  'landscape'
]

