              Se  a sua primeira vez aqui, clique em prximo para seguir o nosso guia.
            `,
            params: {
              placement: 'bottom'
            }
          },
          {
            target: '.artifacts-generator',
            content: `A criao  composta de 4 etapas:
              mecnicas, aparncia, nome e materializao`,
