              Prticando: selecione uma imagem e modifique como achar melhor.`,
            params: {
              placement: 'left'
            }
          },
          {
            target: '.generate-options',
            content: `<strong>Nome e Criao</strong><br/><br/>
              Aqui voc poder dar um nome para o seu artefato e aps isto
